<template>
  <div class="app-fold">
    <div
      :class="{ top: true, hasChecked, 'van-hairline--bottom': showTitleLine }"
      @click="toggle"
    >
      <span class="title">{{ title }}</span>
      <van-icon :name="isFold ? 'arrow-up' : 'arrow-down'" />
    </div>
    <div :class="{ 'fold-content': true, isFold }" ref="fold-content">
      <div>
        <slot />
      </div>
    </div>
  </div>
</template>

<script>
import { Collapse, CollapseItem, Icon } from 'vant';

export default {
  name: 'AppFold',
  components: {
    [Collapse.name]: Collapse,
    [CollapseItem.name]: CollapseItem,
    [Icon.name]: Icon,
  },
  props: {
    showTitleLine: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: '--',
    },
    checked: {
      type: [Number, String, Boolean],
      default: '',
    },
    hasChecked: {
      type: Boolean,
      default: false,
    },
    checkboxUseVmodel: {
      type: Boolean,
      default: false,
    },
    defaultFold: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isFold: this.defaultFold || false,
      observer: null,
    };
  },
  methods: {
    setFold(v) {
      this.isFold = v;
    },
    toggle() {
      this.countHeight();
      window.setTimeout(() => {
        this.isFold = !this.isFold;
      }, 0);
      window.setTimeout(() => {
        const foldContent = this.$refs['fold-content'];
        foldContent.style.height = 'auto';
      }, 310);
      this.$emit('click', !this.isFold);
    },
    countHeight() {
      const foldContent = this.$refs['fold-content'];
      const foldContentWrap = foldContent.childNodes[0];
      foldContent.style.height = foldContentWrap.offsetHeight + 'px';
    },
  },
};
</script>

<style lang="scss" scoped>
.app-fold {
  font-size: 14px;
  background: #fff;
  .top {
    display: flex;
    padding: 15px;
    align-items: center;
  }
  .van-icon {
    color: #8a8a8a;
  }

  .hasChecked {
    position: relative;
    padding-left: 32px;
    /deep/ .van-icon {
      position: absolute;
      left: 14px;
      top: 18px;
    }
  }
  .checkbox {
    position: relative;
    margin-right: 6px;
    z-index: 1;
  }
  .title {
    flex: 1;
  }
  .fold-content {
    overflow: hidden;
    transition: height 0.3s ease-in-out;
  }
  .fold-content.isFold {
    height: 0 !important;
    padding-top: 0;
    padding-bottom: 0;
  }
}
</style>
